.search__input {
  display: flex;
  align-items: center;
  border: 1px solid lightgray;
  height: 30px;
  padding: 10px 20px;
  border-radius: 999px;
  width: 75vw;
  margin: 0 auto;
  margin-top: 40px;
  max-width: 560px;
}
.search__input > input {
  flex: 1;
  padding: 10px 20px;
  font-size: medium;
  border: none;
  outline: none;
  /* we can also use 
   .search__input > input :focus{
       outline-width: 0;
   } */
}
.search__inputIcon {
  color: gray;
}
.search__buttons {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
.search__buttons button {
  margin: 5px;
  padding: 7px 15px;
  background-color: #f8f8f8;
  border: 1px solid rgb(248, 248, 244);
  text-transform: inherit;
  color: #5f6368;
}
.search__buttons button:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
  background-color: #f8f8f8;
  border: 1px solid #c6c6c6;
  color: #222;
  background-image: -webkit-linear-gradient(top, #f5f1f1, #f1f1f1);
}
.search__buttonsHidden {
  display: none !important;
}
